body {
    font-family: 'Poppins', sans-serif;
    background-color: $main-white;
}

h1{
    @include adaptiv-vaule("font-size", 37, 25, 1);
}

h2{
    @include adaptiv-vaule("font-size", 40, 35, 1);
}

.container{
    width: 100%;
    max-width: 1512px;
    margin: 0 auto;
    @include adaptiv-vaule("padding-right", 70, 20, 1);
    @include adaptiv-vaule("padding-left", 70, 20, 1);
}

.img-wrap{
    position: relative;

    img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.main-btn{
    position: relative;
    font-size: 20px;
    width: max-content;

    &::before{
        content:'';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 0;
        height: 2px;
        background-color: $text-color;
        transition: all .3s ease 0s;
    }

    &:hover{
        &::before{
            width: 100%;
        }
    }
}

/* ____________________________#main_________________________ */

.main{
    padding-top: 130px;
    display: flex;

    @include media ('<desktop'){
        padding-top: 90px;
    }

    @include media ('<phone'){
        padding-top: 50px;
    }

    .container{
        padding-right: 0;

        @include media ('<desktopS'){
            padding: 0;
        }
    }

    .content{
        min-height: calc(100vh - 130px);
        display: flex;
        justify-content: space-between;

        @include media ('<desktop'){
            min-height: calc(100vh - 90px);
        }

        @include media ('<desktopS'){
            min-height: 0;
            flex-direction: column-reverse;
            justify-content: start;
        }
    }

    .text{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        width: 27.5%;
        padding: 20px 0;

        @include media ('<desktopS'){
            width: 100%;
            padding: 50px 0;
            @include adaptiv-vaule("padding-right", 70, 20, 1);
            @include adaptiv-vaule("padding-left", 70, 20, 1);
        }

        @include media ('<phone'){
            padding-top: 35px;
            padding-bottom: 35px;
        }
    }

    h1{
        line-height: 1.5;

        @include media ('<desktopS'){
            margin-bottom: 35px;
        }
    }

    p{
        @include media ('<desktopS'){
            margin-bottom: 55px;
        }
    }

    .img-wrap{
        width: 70%;
        min-height: 100%;
        max-width: 1008px;

        @include media ('<desktopS'){
            width: 100%;
            height: 400px;
        }

        @include media ('<phone'){
            height: 320px;
        }
    }
}

/* ____________________________#service_________________________ */

.service{

    .container{
        padding: 0;
    }

    .content{
        display: flex;

        @include media ('<desktopS'){
            flex-direction: column;
        }
    }

    .block{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 33.7%;
        max-width: 505px;
        background-color: $main-orage;
        padding: 20px 0 80px 0;
        @include adaptiv-vaule("padding-left", 70, 20, 1);
        @include adaptiv-vaule("padding-right", 70, 20, 1);

        @include media ('<desktop'){
            padding-top: 20px;
            padding-bottom: 20px;
        }

        @include media ('<desktopS'){
            width: 100%;
            max-width: 100%;
            text-align: center;
            padding-top: 35px;
            padding-bottom: 35px;
        }
    }
    
    .title-wrap{
        p{
            color: $white;
            max-width: 270px;

            @include media ('<desktopS'){
                max-width: 100%;
            }
        }
    }

    .controls{
        position: relative;
        height: 100px;

        @include media ('<desktopS'){
            height: 0;
        }
    }

    .swiper-button-next, .swiper-button-prev{
        z-index: 2;
        color: $white;
        
        @include media ('<desktopS'){
            top: 300px;

            &::after{
                font-size: 50px;
                font-weight: 700;
            }
        }
        
    }

    .swiper-pagination{
        color: $text-color;
        font-size: 72px;
        font-weight: 700;
        letter-spacing: -8px;
        z-index: 1;

        @include media ('<desktopS'){
            display: none;
        }

        span{
            &:first-child{
                color: $white;
            }
        }
    }

    .service-slider{
        width: 67.3%;
        overflow: hidden;

        @include media ('<desktopS'){
            width: 100%;
        }
    }
    
    .item{
        display: flex;
        min-height: 370px;

        @include media ('<desktop'){
            min-height: 235px;
        }

        @include media ('<desktopS'){
            flex-direction: column;
        }
    }

    h3{
        @include adaptiv-vaule("font-size", 40, 35, 1);
    }

    .img-wrap{
        width: 50%;

        @include media ('<desktopS'){
            height: 430px;
            width: 100%;
        }
    }

    .text-wrap{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 50%;
        padding: 30px 30px 55px 55px;

        @include media ('<desktop'){
            padding: 20px 0 20px 35px;
            @include adaptiv-vaule("padding-right", 70, 20, 1);
        }

        @include media ('<desktopS'){
            width: auto;
            @include adaptiv-vaule("padding-left", 70, 20, 1);
        }
    }
}

/* ____________________________#news_________________________ */

.news{

    .container{
        padding-right: 0;

        @include media ('<desktopS'){
            padding: 0;
        }
    }

    .content{
        display: flex;

        @include media ('<desktopS'){
            flex-direction: column-reverse;
        }
    }

    .partners{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 62.3%;
        padding: 140px 20px 40px 0;

        @include media ('<desktopS'){
            width: 100%;
            max-width: 100%;
            @include adaptiv-vaule("padding-right", 70, 20, 1);
            @include adaptiv-vaule("padding-left", 70, 20, 1);
        }
    }

    .item{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30%;
        height: 110px;
        margin-bottom: 100px;

        @include media ('<desktop'){
            width: 43%;
        }
        
        @include media ('<phone'){
            width: 100%;
        }
    }

    .text{
        width: 38.7%;
        max-width: 545px;
        background: $main-orage;
        padding: 60px 0 60px 50px;
        @include adaptiv-vaule("padding-right", 70, 20, 1);

        @include media ('<1125px'){
            width: 39.7%;
        }

        @include media ('<desktop'){
            padding: 20px 0 20px 35px;
            @include adaptiv-vaule("padding-right", 70, 20, 1);
        }

        @include media ('<desktopS'){
            width: 100%;
            max-width: 100%;
            @include adaptiv-vaule("padding-left", 70, 20, 1);
        }

        h2{
            @include media ('<desktopS'){
                text-align: center;
            }
        }
    }
}

/* ____________________________#contact_________________________ */

.contact{
    padding: 80px 0 150px 0;
    background: url('../images/main-page/contact-bg.jpg') 0 0/cover no-repeat;

    @include media ('<desktop'){
        padding: 70px 0;
    }

    @include media ('<phone'){
        padding: 50px 0;
    }

    .content{
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include media ('<desktopS'){
            flex-direction: column;
        }
    }

    .text{
        max-width: 470px;
        margin-right: 40px;

        @include media ('<desktopS'){
            max-width: 100%;
            margin: 0 0 90px 0;
        }
    }

    h2, p{
        color: $white;
    }
}

.form-wrap{
    display: flex;
    justify-content: center;
    width: 100%;

    form{
        display: flex;
        flex-direction: column;
    
        @include media ('<540px'){
            width: 100%;
        }
    }
    
    input, textarea{
        width: 100vw;
        max-width: 470px;
        padding: 17px;
        font-size: 20px;
        color: $main-orage;
        margin-bottom: 10px;
        border: none;
    
        &:active,
        &:focus{
            border: none;
        }
    
        @include media ('<540px'){
            width: 100%;
            max-width: 100%;
        }
    }
    
    textarea{
        min-height: 130px;
    }
    
    button{
        margin: 0 auto;
        min-width: 191px;
        padding: 16px;
        background-color: $main-orage;
        color: $white;
        font-weight: 700;
        font-size: 22px;
        border: none;
        transition: all .3s ease 0s;
        
        &:hover{
            background-color: $black;
        }
    }
}


/* ____________________________#work_________________________ */

.page{
    padding-top: 130px;

    @include media ('<desktop'){
        padding-top: 90px;
    }

    @include media ('<phone'){
        padding-top: 50px;
    }
}

.title-page{
    color: $white;
    background-color: $text-color;
    margin: 0;
    @include adaptiv-vaule("padding-top", 40, 30, 1);
    @include adaptiv-vaule("padding-bottom", 40, 30, 1);
    @include adaptiv-vaule("padding-left", 70, 20, 1);
    @include adaptiv-vaule("padding-right", 70, 20, 1);
}

.work{
       
    .container{
        padding: 0;
    }

    .item{
        display: flex;
        justify-content: space-between;
        min-height: 770px;

        @include media ('<desktopS'){
            flex-direction: column;
        }

        &:nth-child(even){
            flex-direction: row-reverse;

            @include media ('<desktopS'){
                flex-direction: column;
            }
        }
    }

    .img-wrap{
        width: 66%;

        @include media ('<desktopS'){
            height: 400px;
            width: 100%;
        }
    }

    .text{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 34%;
        background-color: $main-orage;
        padding: 60px 0;
        @include adaptiv-vaule("padding-left", 50, 20, 1);
        @include adaptiv-vaule("padding-right", 70, 20, 1);

        @include media ('<desktopS'){
            width: 100%;
        }

        h2{
            margin-bottom: 50px;
        }

        p{
            color: $black;
        }
    }

    .markers{
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 50px;
    }

    .has{
        font-weight: 300;
        font-size: 20px;
        color: $white;
        background-color: $text-color;
        padding: 5px 15px;
        margin:0 20px 10px 0;

        @include media ('<desktop'){
            font-size: 16px;
        }
    }

    .main-btn{
        margin-bottom: 2px;

        @include media ('<phone'){
            margin: 0 auto;
        }
    }
}

/* ____________________________#cross_________________________ */

.cross{
    padding-bottom: 80px;

    @include media ('<desktopS'){
        padding-bottom: 50px;
    }

    .container{
        padding: 0;
    }

    .cross-slider{
        display: flex;
    }

    .item{
        width: 33.3%;

        @include media ('<desktopS'){
            width: 100%;
        }
    }

    .img-wrap{
        min-height: 740px;

        @include media ('<desktopS'){
            min-height: 655px;
        }
    }

    .text{
        text-align: center;
        padding: 20px;

        h2{
            font-size: 22px;
            margin-bottom: 10px;

            @include media ('<desktop'){
               font-size: 25px;
            }
        }

        p{
            font-weight: 300;
            font-size: 16px;
            margin-bottom: 20px;

            @include media ('<desktop'){
                font-size: 14px;
            }
        }

        .main-btn{
            @include media ('<desktopS'){
                font-size: 30px;
            }
        }
    }

    .swiper-pagination{
        display: none;

        @include media ('<desktopS'){
            display: block;
            position: relative;
            padding: 30px 0;
        }

        .swiper-pagination-bullet-active{
            background-color: $text-color;
        }

    }
}

/* ____________________________#supplier_________________________ */

.supplier{

    .container{
        padding: 0;
    }

    .content{
        display: flex;

        @include media ('<desktopS'){
            flex-direction: column;
        }
    }

    .titel{
        width: 40%;
        @include adaptiv-vaule("padding-top", 90, 35, 1);
        @include adaptiv-vaule("padding-bottom", 90, 35, 1);
        @include adaptiv-vaule("padding-left", 70, 20, 1);
        @include adaptiv-vaule("padding-right", 95, 20, 1);
        background: url('../images/cross-page/bg.jpg') 0 0/cover no-repeat;

        @include media ('<desktopS'){
            width: 100%;
        }

        h2{
            font-size: 36px;
            color: $main-orage;
            line-height: 1.5;

            @include media ('<desktopS'){
                text-align: center;
            }
        }
    }

    .block{
        width: 60%;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top: 70px;
        @include adaptiv-vaule("padding-bottom", 100, 50, 1);
        @include adaptiv-vaule("padding-left", 70, 20, 1);
        @include adaptiv-vaule("padding-right", 70, 20, 1);

        @include media ('<desktopS'){
            width: 100%;
        }
    }

    .item{
        max-width: 690px;
        text-align: center;

        img{
            margin-bottom: 25px;
        }

        h3{
            color: $black;
            line-height: 1.5;
            margin-bottom: 25px;

            @include media ('<tablet'){
                font-size: 20px;
            }
        }

        p{
            font-size: 16px;
            font-weight: 300;
            margin-bottom: 35px;

            @include media ('<desktop'){
                font-size: 14px;
            }
        }
    }
}

/* ____________________________#benefits_________________________ */

.benefits{
    background-color: $main-orage;
    @include adaptiv-vaule("padding-top", 85, 70, 1);
    @include adaptiv-vaule("padding-bottom", 115, 60, 1);

    .content{
        display: flex;

        @include media ('<tablet'){
            flex-direction: column
        }
    }

    .title{
        width: 40%;
        padding-right: 100px;

        @include media ('<desktopS'){
            padding-right: 50px
        }

        @include media ('<tablet'){
            width: 100%;
            padding: 0;
            margin-bottom: 90px;
        }
        
        h2{
            @include adaptiv-vaule("margin-bottom", 60 , 30, 1);
        }
        
        p{
            max-width: 330px;
            color: $text-color;
            @include adaptiv-vaule("font-size", 16, 14, 1);

            @include media ('<desktopS'){
                max-width: 100%;
            }
        }
    }

    .block{
        width: 60%;
        padding-left: 60px;

        @include media ('<desktopS'){
            padding-left: 50px;
        }

        @include media ('<tablet'){
            width: 100%;
            padding: 0;
        }

        h3{
            font-weight: 600;
            max-width: 420px;
            font-size: 20px;
            line-height: 1.5;
            margin-bottom: 65px;

            @include media ('<tablet'){
               max-width: 100%;
            }
        }
    }

    .item-wrap{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @include media ('<tablet'){
            justify-content: center;
        }
    }

    .item{
        display: flex;
        width: 44%;
        max-width: 370px;
        margin-bottom: 75px;

        @include media ('<1250px'){
            width: 100%;
        }

        @include media ('<phone'){
            flex-direction: column
        }

        p{
            font-size: 14px;
            line-height: 1.5;
            font-weight: 300;
            color: $text-color;
        }
    }
    
    .icon-wrap{
        text-align: center;
        margin-right: 30px;
        min-width: 45px;

        @include media ('<phone'){
            margin: 0 0 40px 0;
            min-height: 100px;
        }

        img{
            @include media ('<phone'){
                height: 100%;
            }
        }
    }
}

/* ____________________________#registr_________________________ */

.registr{

    .container{
        padding: 0;
    }

    .block{
        display: flex;

        @include media ('<desktopS'){
            flex-direction: column;
        }
    }

    .registr-form{
        width: 70%;
        display:flex;
        flex-direction: column;
        align-items: center;
        @include adaptiv-vaule("padding-top", 90, 45, 1);
        @include adaptiv-vaule("padding-bottom", 90, 45, 1);
        @include adaptiv-vaule("padding-left", 70, 20, 1);
        padding-right: 20px;
        background: url('../images/registr-bg.jpg') 0 0/cover no-repeat;

        @include media ('<1250px'){
            width: 60%;
        }

        @include media ('<desktopS'){
            width: 100%;
            @include adaptiv-vaule("padding-right", 70, 20, 1);
        }

        h2{
            max-width: 650px;
            color: $white;
            text-align: center;
            @include adaptiv-vaule("margin-bottom", 95 , 45, 1);
        }

        button{
            background-color: $text-color;

            &:hover{
                background-color: $black;
            }
        }
    }

    .text{
        width: 30%;
        @include adaptiv-vaule("padding-top", 70, 40, 1);
        @include adaptiv-vaule("padding-bottom", 70, 40, 1);
        @include adaptiv-vaule("padding-right", 70, 20, 1);
        padding-left: 30px;

        @include media ('<1250px'){
            width: 40%;
        }

        @include media ('<desktopS'){
            width: 100%;
            @include adaptiv-vaule("padding-left", 70, 20, 1);
        }
    }

    .active-form{
        @include adaptiv-vaule("margin-bottom", 55 , 40, 1);

        p{
            font-size: 15px;
            color: $black;
            margin: 0;

            @include media ('<desktopS'){
                text-align: center;
            }

            @include media ('<phone'){
                text-align: left;
            }
        }

        form{
            width: 100%;

            @include media ('<desktopS'){
                max-width: 470px;
                margin: 0 auto;
            }
        }

        input{
            width: 100%;
            max-width: 345px;
            border: 1px solid #BEA363;
            padding: 20px;
            color: $black;
            margin-bottom: 30px;

            @include media ('<desktopS'){
                text-align: center;
                max-width: 100%;
            }
        }

        button{
            min-width: 230px;
            padding: 14px;
            color: $text-color;
            text-align: center;
            background-color: $main-orage;
            border: none;
            font-weight: 300;
            font-size: 22px;
            transition: all .3s ease 0s;

            @include media ('<desktop'){
                min-width: 100%;
            }

            &:hover{
                background-color: $text-color;
                color: $white;
            }
        }
    }

    .namber-list, .dots-list{
        padding: 0;
        margin: 0;
        font-weight: 300;
        font-size: 18px;
        color: black;
        line-height: 1.6;
        margin-bottom: 20px;
    }

    .namber-list{
        li{
            padding-left: 5px;
            margin-left: 20px;
        }
    }

    .dots-list{
        li{
            margin-left: 30px;
        }
    }
}